<template>
  <div id="app">
    <div class="main-container">
        <ItemAnim>
            <Talker
            :message="currentMessage"
            :key="currentMessage"
            ></Talker>
        </ItemAnim>
        <img class="xixi" :src="xixiPngUrl" alt="xixi">
    </div>
    <div class="tool-container">
        <ListAnim>
            <Tool
                v-for="tool in visibleToolList"
                :key="tool.id"
                :name="tool.name"
                :vis="tool.visible"
                :clickValue="tool.clickValue"
                @toggleTool="toggleToolList"
                @calHP="calHealth"
            ></Tool>
        </ListAnim>
    </div>
    <Scene></Scene>
  </div>
</template>

<script>
import Scene from "./components/Scene.vue";
import Talker from "./components/Talker.vue";
import Tool from './components/Tool.vue'
import ListAnim from './components/ListAnim.vue'
import ItemAnim from './components/ItemAnim.vue'

const myName = ""
const herName = ""

export default {
    name: "app",
    mounted() {
        let vm = this
        setInterval(function () {
            let messageIndex = Math.floor(Math.random() * vm.defaultMessageList.length)
            vm.setCurrentMessage(vm.defaultMessageList[messageIndex])
        },5000)
    },
    data() {
        return {
            HP: 100,
            xixiPngUrl: "./src/assets/xixi.png",
            defaultMessageList: [
                `${ herName }`, 
                `${ myName }`,
                "",
                ""
            ],
            currentMessage: `${ herName }`,
            toolList: [
                {
                    id: 1,
                    name: "util",
                    visible: true,
                    clickValue: 0
                },
                {
                    id: 2,
                    name: "sad",
                    visible: false,
                    clickValue: -1
                },
                {
                    id:3,
                    name: "love",
                    visible: false,
                    clickValue: 10
                }
            ],
        };
    },
    methods: {
        setCurrentMessage(msg) {
            this.currentMessage = msg
        },
        toggleToolList() {
            this.shouldHiddenToolList.forEach(tool => {
                tool.visible = !tool.visible
            })
        },
        calHealth(hpChange) {
            this.HP = Math.min((this.HP + hpChange), 100)
            if (hpChange !== 0){
                let hpChangeStr = hpChange > 0 ? "+" + String(hpChange) : String(hpChange)
                this.setCurrentMessage(`${ hpChangeStr }`)
            }
            
        }
    },
    computed: {
        visibleToolList() {
            return this.toolList.filter(tool => {
                return tool.visible
            })
        },
        shouldHiddenToolList() {
            return this.toolList.filter(tool => {
                return tool.name !== "util"
            })
        }
    },
    components: {
        Scene,
        Talker,
        Tool,
        ListAnim,
        ItemAnim
    }
};
</script>

<style>
#app {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    position: relative;
}

html,
body {
    margin: 0;
    height: 100%;
}

* {
    box-sizing: border-box;
    overflow: hidden;
}

a {
    color: #42b983;
}

.main-container {
    position: absolute;
    width: 100%;
    height: 100%;
    background: transparent;
    pointer-events: none;
}

.tool-container {
    position: absolute;
    bottom: 5%;
    width: 100%;
}

.xixi {
    position: absolute;
    top: 0;
    right: 0;
    width: 30%;
}

.clearfix {
    overflow: auto;
}

</style>



